export interface IToken {
  tokenId: string
  tokenNumber: string
  tokenStatus: {
    statusId: string
    statusDescripcion: string
  }
  device : {
    deviceId: string
    deviceNumber: string
    deviceType : string
    deviceDescription : string
  }
  wallet : {
    walleId: string
    walleDescripcion: string
  }
}

export class Token implements IToken {
  constructor(
    public tokenId = '',
    public tokenNumber = '',
    public tokenStatus = {
      statusId: '',
      statusDescripcion: '',
    },
    public device = {
      deviceId: '',
      deviceNumber: '',
      deviceType : '',
      deviceDescription : '',
    },
    public wallet = {
      walleId: '',
      walleDescripcion: '',
    }

  ) {}

  static Build(token: IToken) {
    if (!token) {
      return new Token()
    }

    return new Token(
      token.tokenId,
      token.tokenNumber,
      token.tokenStatus,
      token.device,
      token.wallet
    )
  }


  toJSON(): object {
    const serialized = Object.assign(this)
    return serialized
  }
}
