import { ActivatedRoute, ParamMap } from '@angular/router';
import {
  AfterViewInit,
  Component,
  OnDestroy,
  OnInit,
  ViewChild
} from '@angular/core';
import { BehaviorSubject, Observable, Subject, merge, of } from 'rxjs';
import { ITokens, TokenService } from '../../business-api/token/token.service';
import {
  catchError,
  debounceTime,
  map,
  startWith,
  switchMap
} from 'rxjs/operators';

import { FormControl } from '@angular/forms';
import { IToken } from '../../models/token';
import { SubSink } from 'subsink';

@Component({
  selector: 'omnichannel2-commercial-plataform-wallet-management-token-table',
  templateUrl: './token-table.component.html',
  styleUrls: ['./token-table.component.css']
})
export class TokenTableComponent implements OnDestroy, AfterViewInit {
  displayedColumns = [
    'walletDescripcion',
    'deviceType',
    'deviceDescription',
    'TokenNumber',
    'tokenStatus'
  ];
  items$: Observable<IToken[]>;
  hasError = false;
  errorText = '';
  private skipLoading = false;
  private subs = new SubSink();
  useNgRxData = false;
  readonly isLoadingResults$ = new BehaviorSubject(true);
  loading$: Observable<boolean>;
  refresh$ = new Subject();

  constructor(
    private tokenService: TokenService,
    private activatedRoute: ActivatedRoute
  ) {}
  cardId: string;
  customerId: string;

  getTokens(customerId: string, cardId: string): Observable<ITokens> {
    return this.tokenService.getTokens(customerId, cardId);
  }

  ngOnDestroy(): void {
    this.subs.unsubscribe();
  }

  ngAfterViewInit() {
    this.activatedRoute.paramMap.subscribe((parametros: ParamMap) => {
      this.cardId = parametros.get('cardId');
      this.customerId = parametros.get('customerId');
    });

    if (this.skipLoading) {
      return;
    }

    this.items$ = merge(this.refresh$).pipe(
      startWith({}),

      switchMap(() => {
        this.isLoadingResults$.next(true);
        return this.getTokens(this.customerId, this.cardId);
      }),
      //map((results: { message: { response: IToken[] } } ) => {
        map((results: { total: number; data: IToken[] }) => {
        this.isLoadingResults$.next(false);
        this.hasError = false;
        //return results.message.response;
        return results.data;
      }),
      catchError(err => {
        this.isLoadingResults$.next(false);
        this.hasError = true;
        this.errorText = err;
        return of([]);
      })
    );
    this.items$.subscribe();
  }
}
